import React, { Component } from 'react'
import {NavLink} from 'react-router-dom'
import {Button} from 'antd';
export default class Sider extends Component {
    render() {
        let style={float:'left'}
        return (
             <ul style={style}>
                <Button block>按钮</Button>
                {/* <li>
                    <NavLink activeStyle={{background:'red',color:'#fff'}}to={{pathname:'/hoc',state:{id:100},search:'?user=zhangsan'}}>Hoc</NavLink>
                    <NavLink activeStyle={{background:'red',color:'#fff'} }to='/parent'>Parent</NavLink>
                </li> */}
                {
                    [1,2,3,4,5,6,7,8].map((item)=>(
                        <li key={item}>
                            <NavLink activeStyle={
                                {background:'red',
                                color:'#fff'}
                            } to={'/content/'+item}>{item}</NavLink>
                        </li>
                    ))
                }
                {/* {
                     [1,2,3,4,5,6].map((item)=>(
                        <li key={item}>
                            <NavLink activeStyle={
                                {background:'red',
                                color:'#fff'}
                            } to={'/get/'+item}>首页{item}</NavLink>
                        </li>
                    ))
                } */}
             </ul>
        )
    }
}
