import React, { Component } from 'react'
import {HashRouter as Router,Route,Switch,Redirect,withRouter} from 'react-router-dom';
import Hoc from './Hoc/Hoc';
import Parent from './Context/Parent';
import NoMatch from'./NoMatch';
import Content from'./Router1/Content';
import Sider from './Sider';
function Login(props){
    console.log(props);
    return <button>登录</button>
}
let LoginWithRouter=withRouter(Login);
export default class App extends Component {
    render() {
        return (
            <Router basename="/build">
                <div>
                    <Sider/>
                    <div style={{float:'left',border:'2px solid red',marginLeft:100}}>
                    <Switch>
                        {/* <Route path='/hoc' component={Hoc} /> */}
                        <Route path='/content/:id' component={Content}/>
                        {/* <Route path='/get/:id' component={Content}/> */}
                        <Route>
                            <NoMatch/>
                        </Route>
                    </Switch>{/**先匹配上面能匹配的，再出最后的页面 */}
                    </div>
                     
                </div>  
            </Router>
        )
    }
}