import React, { Component } from 'react'
import ReactDOM from'react-dom';
export default class Protal extends Component {
    handleClick=()=>{
        console.log('portalClick');
    }
    render() {
        return (
            ReactDOM.createPortal(
                <div onClick={this.handleClick}>
                    <h1>Portal</h1>
                </div>,
                document.body
            )
        )
    }
}
