import React, { Component } from 'react'
import {con} from'./Context'
export default class Child extends Component {
    render() {
        return (
            <div>
                Child组件
                <div>{this.context}</div>
                {/* <con.Consumer>
                     {
                         (id)=><div>{id}</div>//是个函数，函数的参数是传过来的value值
                     }
                </con.Consumer> */}
            </div>
        )
    }
}
Child.contextType=con;
